.projects {
  padding-top: 10vh;
  /* max-height: 380vh;
  min-width: 100%;
  max-width: 200%;
  min-height: 250vh; */
}
.projects_container {
  background-color: #eee;
  display: grid;
  padding: 30px;
  place-items: center;
  border-radius: 2px;
  padding-bottom: 40px;
  /* margin-right: 20px; */
  padding-left: 22px;
  margin-bottom: 80px;
}
img {
  margin-top: 20vh;
  min-width: 50vw;
  max-width: 80vw;
  max-height: 50vh;
  min-height: 20vh;
}
.s {
  color: white;
  font-size: 80px;
}
.icon {
  transition: 0.4s ease;
  display: grid;
  place-items: center;
  margin-bottom: 10vh;
  border-radius: 4px;
  margin-left: 30px;
  margin-right: 30px;
}
.projects_btns {
  display: flex;
  background-color: #eee;
  margin-top: 20px;
  margin-bottom: 20px;
}
.btns {
  border: none;
  cursor: pointer;
  outline: none;
  transition: 250ms;
  padding: 20px;
  background: none;
}
.projects_header {
  width: 100%;
  background: none;
  text-align: center;
  margin-top: 1vh;
}

.start_projects {
  transition: 0.4s ease-in;
}
/* @media only screen and (max-width: 480px) {
  .projects_container {
    margin-left: -10px;
  }
} */
.projects_container {
  transition: 250ms;
  animation-name: container;
  animation-duration: 2s;
}
@keyframes container {
  0% {
    margin-left: -40vw;
    opacity: 0;
  }
  100% {
    margin-left: 0vw;
    opacity: 1;
  }
}
.btns:hover {
  color: #eee;
  background-color: black;
}
.pro_head {
  text-align: center;
}
.page_links {
  color: black;
  text-decoration: none;
}
.start_projects {
  cursor: pointer;
}
