.contact {
  background-color: #14213d;
  height: 100vh;
  display: grid;
  place-items: center;
}
.contact_add {
  display: flex;
}
.hh {
  color: white;
  padding: 30px;
  border-radius: 50px;
  transition: 0.2s;
}
.hh:hover {
  background-color: white;
  color: #14213d;
}
@media only screen and (max-width: 700px) {
  .contact_add {
    display: grid;
    place-items: center;
  }
}
