.contact {
  background-color: #14213d;
  height: 100vh;
  display: grid;
  place-items: center;
}
.contact_add {
  display: -webkit-flex;
  display: flex;
}
.hh {
  color: white;
  padding: 30px;
  border-radius: 50px;
  transition: 0.2s;
}
.hh:hover {
  background-color: white;
  color: #14213d;
}
@media only screen and (max-width: 700px) {
  .contact_add {
    display: grid;
    place-items: center;
  }
}

.header {
  width: 100%;
  min-height: 10vh;
  max-height: 10.6vh;
  background-color: #03045e;
  overflow: hidden;
}
.nav {
  position: absolute;
  top: 3.45%;
  right: 4%;
}
.navlink {
  padding: 19px;
  color: #caf0f8;
  transition: 250ms;
  text-decoration: none;
}
.navlink:hover {
  -webkit-animation: hover;
          animation: hover;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;
}

@-webkit-keyframes hover {
  0% {
    color: white;
    border-bottom: 2px solid white;
    width: 0%;
  }
  25% {
    color: white;

    width: 25%;
    margin-right: 2px;
    border-bottom: 2px solid white;
  }
  50% {
    margin-right: 2px;
    color: white;

    width: 50%;
    border-bottom: 2px solid white;
  }
  75% {
    margin-right: 2px;
    color: white;

    border-bottom: 2px solid white;
    width: 75%;
  }
  100% {
    margin-right: 2px;
    color: white;
    border-bottom: 2px solid white;

    width: 100%;
  }
}

@keyframes hover {
  0% {
    color: white;
    border-bottom: 2px solid white;
    width: 0%;
  }
  25% {
    color: white;

    width: 25%;
    margin-right: 2px;
    border-bottom: 2px solid white;
  }
  50% {
    margin-right: 2px;
    color: white;

    width: 50%;
    border-bottom: 2px solid white;
  }
  75% {
    margin-right: 2px;
    color: white;

    border-bottom: 2px solid white;
    width: 75%;
  }
  100% {
    margin-right: 2px;
    color: white;
    border-bottom: 2px solid white;

    width: 100%;
  }
}

