@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");

.body {
  background-color: #48cae4;
  width: 100%;
  height: 90vh;
}
.body_heading {
  display: grid;
  place-items: center;
}
@keyframes title {
  0% {
    margin-left: -50vh;
    opacity: 0.2;
  }
  /* 25% {
    margin-left: -30vw;
  }
  50% {
    margin-left: -20vw;
  }
  75% {
    margin-left: -10vw;
  } */
  100% {
    opacity: 1;
    margin-left: 0vh;
  }
}
.title {
  display: flex;
  animation-duration: 2.4s;
  transition: 200ms;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  color: #457b9d;
  margin-top: 32vh;
  font-size: 60px;
}
@keyframes des {
  0% {
    opacity: 0;

    margin-left: -50vh;
  }
  100% {
    opacity: 1;

    margin-bottom: 0vh;
  }
}
.des {
  font-family: "Lobster", cursive;
  animation-name: des;
  animation-duration: 1.6s;
  transition: 200ms;
  font-size: 36px;
  margin-top: 6vh;
  color: #1d3557;
  font-size: 20px;
}

@media only screen and (max-width: 400px) {
  .title {
    font-size: 40px;
  }
}
